









import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';

@Component
export default class CSelectorSample extends Vue {
  // Props
  @Prop() public samples!: {
    name: string,
    caption: string,
    content: string,
  }[];
  @Prop() public sampleIndex!: number;

  // Data
  private dataSamples: {
    name: string,
    caption: string,
    content: string,
  }[] = this.samples;
  private dataSampleIndex: number = this.sampleIndex;

  // Emit
  @Emit('selectSample')
  private emitSelectSample(): number {
    return this.dataSampleIndex;
  }

  // Methods
  private listenOnSelectSample($event: Event): void {
    this.dataSampleIndex = Number((<HTMLInputElement>(<any>$event).target).value);
    this.emitSelectSample();
  }
}
