

































































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import CWeb from '@/components/links/CWeb.vue';
import CSelectorSample from '@/components/CSelectorSample.vue';

@Component({
  components: {
    CWeb,
    CSelectorSample,
  },
  head: {
    title: {
      inner: 'HTMLエンコーダ',
    },
  },
})
export default class HtmlEncoder extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataAmpCheck: boolean = true;
  private dataTagCheck: boolean = true;
  private dataAposCheck: boolean = true;
  private dataQuotCheck: boolean = true;
  private dataSharpCheck: boolean = false;
  private dataAtCheck: boolean = false;
  private dataDollarCheck: boolean = false;
  private dataBraceCheck: boolean = false;
  private dataQuestionCheck: boolean = false;
  private dataNewlineCheck: boolean = false;
  private dataNewlineTag: string = '<br />';
  private dataOrgText: string = '';
  private dataEncodedText: string = '';
  private dataTargetChars: { targetChar: string, regExp: RegExp, charReference: string }[] = [
    {
      targetChar: 'amp',
      regExp: /\&/g,
      charReference: '&amp;',
    },
    {
      targetChar: 'tag',
      regExp: /\</g,
      charReference: '&lt;',
    },
    {
      targetChar: 'tag',
      regExp: /\>/g,
      charReference: '&gt;',
    },
    {
      targetChar: 'apos',
      regExp: /\'/g,
      charReference: '&apos;',
    },
    {
      targetChar: 'quot',
      regExp: /\"/g,
      charReference: '&quot;',
    },
    {
      targetChar: 'sharp',
      regExp: /\#/g,
      charReference: '&#x23;',
    },
    {
      targetChar: 'at',
      regExp: /\@/g,
      charReference: '&#x40;',
    },
    {
      targetChar: 'dollar',
      regExp: /\$/g,
      charReference: '&#x24;',
    },
    {
      targetChar: 'brace',
      regExp: /\{/g,
      charReference: '&#x7b;',
    },
    {
      targetChar: 'brace',
      regExp: /\}/g,
      charReference: '&#x7d;',
    },
    {
      targetChar: 'question',
      regExp: /\?/g,
      charReference: '&#x3f;',
    },
    {
      targetChar: 'newline',
      regExp: /(\n|\r\n|\r)/g,
      charReference: this.dataNewlineTag,
    },
  ];
  private dataSamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: '',
    },
    {
      name: 'plain',
      caption: '標準的なHTML文書',
      content: '<!DOCTYPE html>\n<html lang="ja">\n  <head>\n    <meta charset="utf-8" />\n    <meta http-equiv="X-UA-Compatible" content="IE=edge" />\n    <meta name="viewport" content="width=device-width,initial-scale=1.0" />\n  </head>\n  <body>\n    <h1>サンプル</h1>\n  </body>\n</html>',
    },
    {
      name: 'php',
      caption: 'PHPソースコード',
      content: '<?php echo "Hello World"; ?>',
    },
    {
      name: 'smarty',
      caption: 'Smartyテンプレート',
      content: '{if $products}\n{foreach from=$products item=product key=key}\n<h3>{$product.title}</h3>\n<p>{$product.content}</p>\n{/foreach}\n{/if}\n{include file="links.tpl"}',
    },
    {
      name: 'blade',
      caption: 'Bladeテンプレート',
      content: '<html>\n  <head>\n    <title>タイトル | @yield(\'title\')</title>\n  </head>\n  <body>\n    @section(\'sidebar\')\n    @show\n    <div class="container">\n      @yield(\'content\')\n    </div>\n  </body>\n</html>',
    },
  ];
  private dataSampleIndex: number = 0;

  // Emit
  @Emit('encode-text')
  private emitEncodeText(): void {}

  // Watch
  @Watch('dataAmpCheck')
  private watchOnAmpChecked(newValue: string, orgValue: string) {
    this.encodeText();
  }
  @Watch('dataTagCheck')
  private watchOnTagChecked(newValue: string, orgValue: string) {
    this.encodeText();
  }
  @Watch('dataAposCheck')
  private watchOnAposChecked(newValue: string, orgValue: string) {
    this.encodeText();
  }
  @Watch('dataQuotCheck')
  private watchOnQuotChecked(newValue: string, orgValue: string) {
    this.encodeText();
  }
  @Watch('dataSharpCheck')
  private watchOnSharpChecked(newValue: string, orgValue: string) {
    this.encodeText();
  }
  @Watch('dataAtCheck')
  private watchOnAtChecked(newValue: string, orgValue: string) {
    this.encodeText();
  }
  @Watch('dataDollarCheck')
  private watchOnDollarChecked(newValue: string, orgValue: string) {
    this.encodeText();
  }
  @Watch('dataBraceCheck')
  private watchOnBraceChecked(newValue: string, orgValue: string) {
    this.encodeText();
  }
  @Watch('dataQuestionCheck')
  private watchOnQuestionChecked(newValue: string, orgValue: string) {
    this.encodeText();
  }
  @Watch('dataNewlineCheck')
  private watchOnNewlineChecked(newValue: string, orgValue: string) {
    this.encodeText();
  }
  @Watch('dataNewlineTag')
  private watchOnNewlineSelected(newValue: string, orgValue: string) {
    this.encodeText();
  }
  @Watch('dataOrgText')
  private watchOnTextInputed(newValue: string, orgValue: string) {
    this.encodeText();
  }

  // Methods
  private listenAmpCheck($event: Event): void {
    this.dataAmpCheck = (<HTMLInputElement>(<any>$event).target).checked;
  }
  private listenTagCheck($event: Event): void {
    this.dataTagCheck = (<HTMLInputElement>(<any>$event).target).checked;
  }
  private listenAposCheck($event: Event): void {
    this.dataAposCheck = (<HTMLInputElement>(<any>$event).target).checked;
  }
  private listenQuotCheck($event: Event): void {
    this.dataQuotCheck = (<HTMLInputElement>(<any>$event).target).checked;
  }
  private listenSharpCheck($event: Event): void {
    this.dataSharpCheck = (<HTMLInputElement>(<any>$event).target).checked;
  }
  private listenAtCheck($event: Event): void {
    this.dataAtCheck = (<HTMLInputElement>(<any>$event).target).checked;
  }
  private listenDollarCheck($event: Event): void {
    this.dataDollarCheck = (<HTMLInputElement>(<any>$event).target).checked;
  }
  private listenBraceCheck($event: Event): void {
    this.dataBraceCheck = (<HTMLInputElement>(<any>$event).target).checked;
  }
  private listenQuestionCheck($event: Event): void {
    this.dataQuestionCheck = (<HTMLInputElement>(<any>$event).target).checked;
  }
  private listenNewlineCheck($event: Event): void {
    this.dataNewlineCheck = (<HTMLInputElement>(<any>$event).target).checked;
  }
  private listenNewlineSelect($event: Event): void {
    this.dataNewlineTag = (<HTMLInputElement>(<any>$event).target).value;
  }
  private listenOnSampleSelect($event: Event): void {
    this.dataOrgText = this.dataSamples[Number($event)].content;
  }
  private listenInput($event: Event): void {
    this.dataOrgText = (<HTMLInputElement>(<any>$event).target).value;
  }
  private encodeText(): void {
    let encodedText = this.dataOrgText;
    this.dataTargetChars.forEach(targetChar => {
      switch (targetChar.targetChar) {
        case 'amp':
          if (this.dataAmpCheck) {
            encodedText = encodedText.replace(targetChar.regExp, targetChar.charReference);
          }
          break;
        case 'tag':
          if (this.dataTagCheck) {
            encodedText = encodedText.replace(targetChar.regExp, targetChar.charReference);
          }
          break;
        case 'apos':
          if (this.dataAposCheck) {
            encodedText = encodedText.replace(targetChar.regExp, targetChar.charReference);
          }
          break;
        case 'quot':
          if (this.dataQuotCheck) {
            encodedText = encodedText.replace(targetChar.regExp, targetChar.charReference);
          }
          break;
        case 'sharp':
          if (this.dataSharpCheck) {
            encodedText = encodedText.replace(targetChar.regExp, targetChar.charReference);
          }
          break;
        case 'at':
          if (this.dataAtCheck) {
            encodedText = encodedText.replace(targetChar.regExp, targetChar.charReference);
          }
          break;
        case 'dollar':
          if (this.dataDollarCheck) {
            encodedText = encodedText.replace(targetChar.regExp, targetChar.charReference);
          }
          break;
        case 'brace':
          if (this.dataBraceCheck) {
            encodedText = encodedText.replace(targetChar.regExp, targetChar.charReference);
          }
          break;
        case 'question':
          if (this.dataQuestionCheck) {
            encodedText = encodedText.replace(targetChar.regExp, targetChar.charReference);
          }
          break;
        case 'newline':
          if (this.dataNewlineCheck) {
            encodedText = encodedText.replace(targetChar.regExp, this.dataNewlineTag + '$1');
          }
          break;
      }
    });
    this.dataEncodedText = encodedText;
  }
}
